import React, { useState, useCallback } from 'react';
import { Camera, User, MapPin, Briefcase, Calendar, Phone, Mail } from 'lucide-react';

interface VolunteerData {
  id: string;
  district: string;
  panchayath: string;
  ward_no: string;
  vol_name: string;
  address: string;
  gender: string;
  dob: string;
  age: string;
  blood_group: string;
  phone: string;
  email: string;
  type: string;
  job: string;
  photo: File | null;
  status: string;
  description: string;
}

const VolunteerRegistrationForm: React.FC = () => {
  const [formData, setFormData] = useState<VolunteerData>({
    id: '',
    district: '',
    panchayath: '',
    ward_no: '',
    vol_name: '',
    address: '',
    gender: '',
    dob: '',
    age: '',
    blood_group: '',
    phone: '',
    email: '',
    type: '',
    job: '',
    photo: null,
    status: '',
    description: ''
  });

  const [errors, setErrors] = useState<Record<string, string>>({});
  const [photoFileName, setPhotoFileName] = useState<string>('');

  const calculateAge = useCallback((dateOfBirth: string): string => {
    if (!dateOfBirth) return '';
    
    const dob = new Date(dateOfBirth);
    const today = new Date();
    
    let age = today.getFullYear() - dob.getFullYear();
    const monthDiff = today.getMonth() - dob.getMonth();
    
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < dob.getDate())) {
      age--;
    }
    
    return age >= 0 ? `${age} years old` : 'Invalid date';
  }, []);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    
    setFormData(prev => {
      const updated = { ...prev, [name]: value };
      
      // Auto-calculate age when DOB changes
      if (name === 'dob') {
        updated.age = calculateAge(value);
      }
      
      return updated;
    });

    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: '' }));
    }
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0] || null;
    setFormData(prev => ({ ...prev, photo: file }));
    setPhotoFileName(file ? file.name : '');
  };

  const validateForm = (): boolean => {
    const newErrors: Record<string, string> = {};
    const requiredFields = ['id', 'vol_name', 'address', 'district', 'panchayath', 'ward_no', 'gender', 'dob', 'phone', 'email', 'type'];
    
    requiredFields.forEach(field => {
      if (!formData[field as keyof VolunteerData]) {
        newErrors[field] = 'This field is required';
      }
    });

    // Email validation
    if (formData.email && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = 'Please enter a valid email address';
    }

    // Phone validation
    if (formData.phone && !/^\d{10}$/.test(formData.phone.replace(/\D/g, ''))) {
      newErrors.phone = 'Please enter a valid 10-digit phone number';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.MouseEvent) => {
    e.preventDefault();
    
    if (validateForm()) {
      console.log('Form Data:', formData);
      alert('Volunteer registration submitted successfully!');
      // Handle form submission here
    }
  };

  const inputClasses = (fieldName: string) => `
    w-full px-4 py-3 border-2 rounded-xl text-base transition-all duration-300 bg-gray-50
    focus:outline-none focus:border-blue-600 focus:bg-white focus:shadow-lg focus:-translate-y-0.5
    ${errors[fieldName] ? 'border-red-500' : 'border-gray-200'}
  `;

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-900 via-blue-800 to-blue-700 p-5">
      <div className="max-w-4xl mx-auto bg-white rounded-3xl shadow-2xl overflow-hidden">
        {/* Header */}
        <div className="bg-gradient-to-r from-blue-900 to-blue-700 text-white p-8 text-center">
          <div className="flex justify-center mb-4">
            <User className="w-16 h-16 text-blue-200" />
          </div>
          <h1 className="text-4xl font-light mb-3">Volunteer Registration</h1>
          <p className="text-xl text-blue-200">Join our community of dedicated volunteers</p>
        </div>

        {/* Form */}
        <div className="p-8">
          <div className="space-y-8">
            {/* Personal Information Section */}
            <div className="bg-gray-50 p-6 rounded-2xl border-l-4 border-blue-600">
              <div className="flex items-center mb-6">
                <User className="w-6 h-6 text-blue-600 mr-3" />
                <h3 className="text-xl font-semibold text-blue-900">Personal Information</h3>
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    ID <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="id"
                    value={formData.id}
                    onChange={handleInputChange}
                    className={inputClasses('id')}
                    placeholder="Enter volunteer ID"
                  />
                  {errors.id && <p className="text-red-500 text-sm mt-1">{errors.id}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Volunteer Name <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="vol_name"
                    value={formData.vol_name}
                    onChange={handleInputChange}
                    className={inputClasses('vol_name')}
                    placeholder="Enter full name"
                  />
                  {errors.vol_name && <p className="text-red-500 text-sm mt-1">{errors.vol_name}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Gender <span className="text-red-500">*</span>
                  </label>
                  <select
                    name="gender"
                    value={formData.gender}
                    onChange={handleInputChange}
                    className={inputClasses('gender')}
                  >
                    <option value="">Select Gender</option>
                    <option value="male">Male</option>
                    <option value="female">Female</option>
                    <option value="others">Others</option>
                  </select>
                  {errors.gender && <p className="text-red-500 text-sm mt-1">{errors.gender}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Date of Birth <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                    <input
                      type="date"
                      name="dob"
                      value={formData.dob}
                      onChange={handleInputChange}
                      className={`${inputClasses('dob')} pl-12`}
                    />
                  </div>
                  {errors.dob && <p className="text-red-500 text-sm mt-1">{errors.dob}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Age
                  </label>
                  <div className={`
                    bg-gradient-to-r from-blue-50 to-blue-100 text-blue-900 p-4 rounded-xl 
                    font-semibold text-center border-2 border-blue-300
                    ${formData.age ? 'from-green-50 to-green-100 text-green-900 border-green-300' : ''}
                    ${formData.age && formData.age.includes('Invalid') ? 'from-red-50 to-red-100 text-red-900 border-red-300' : ''}
                  `}>
                    {formData.age || 'Age will be calculated automatically'}
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Blood Group
                  </label>
                  <select
                    name="blood_group"
                    value={formData.blood_group}
                    onChange={handleInputChange}
                    className={inputClasses('blood_group')}
                  >
                    <option value="">Select Blood Group</option>
                    <option value="A+">A+</option>
                    <option value="A-">A-</option>
                    <option value="B+">B+</option>
                    <option value="B-">B-</option>
                    <option value="AB+">AB+</option>
                    <option value="AB-">AB-</option>
                    <option value="O+">O+</option>
                    <option value="O-">O-</option>
                  </select>
                </div>
              </div>
            </div>

            {/* Contact Information Section */}
            <div className="bg-gray-50 p-6 rounded-2xl border-l-4 border-blue-600">
              <div className="flex items-center mb-6">
                <MapPin className="w-6 h-6 text-blue-600 mr-3" />
                <h3 className="text-xl font-semibold text-blue-900">Contact Information</h3>
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="md:col-span-2">
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Address <span className="text-red-500">*</span>
                  </label>
                  <textarea
                    name="address"
                    value={formData.address}
                    onChange={handleInputChange}
                    rows={4}
                    className={inputClasses('address')}
                    placeholder="Enter your complete address"
                  />
                  {errors.address && <p className="text-red-500 text-sm mt-1">{errors.address}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    District <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="district"
                    value={formData.district}
                    onChange={handleInputChange}
                    className={inputClasses('district')}
                    placeholder="Enter district"
                  />
                  {errors.district && <p className="text-red-500 text-sm mt-1">{errors.district}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Panchayath <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="panchayath"
                    value={formData.panchayath}
                    onChange={handleInputChange}
                    className={inputClasses('panchayath')}
                    placeholder="Enter panchayath"
                  />
                  {errors.panchayath && <p className="text-red-500 text-sm mt-1">{errors.panchayath}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Ward Number <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="ward_no"
                    value={formData.ward_no}
                    onChange={handleInputChange}
                    className={inputClasses('ward_no')}
                    placeholder="Enter ward number"
                  />
                  {errors.ward_no && <p className="text-red-500 text-sm mt-1">{errors.ward_no}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Phone Number <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                    <input
                      type="tel"
                      name="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                      className={`${inputClasses('phone')} pl-12`}
                      placeholder="Enter phone number"
                    />
                  </div>
                  {errors.phone && <p className="text-red-500 text-sm mt-1">{errors.phone}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Email Address <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      className={`${inputClasses('email')} pl-12`}
                      placeholder="Enter email address"
                    />
                  </div>
                  {errors.email && <p className="text-red-500 text-sm mt-1">{errors.email}</p>}
                </div>
              </div>
            </div>

            {/* Professional Information Section */}
            <div className="bg-gray-50 p-6 rounded-2xl border-l-4 border-blue-600">
              <div className="flex items-center mb-6">
                <Briefcase className="w-6 h-6 text-blue-600 mr-3" />
                <h3 className="text-xl font-semibold text-blue-900">Professional Information</h3>
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Volunteer Type <span className="text-red-500">*</span>
                  </label>
                  <select
                    name="type"
                    value={formData.type}
                    onChange={handleInputChange}
                    className={inputClasses('type')}
                  >
                    <option value="">Select Type</option>
                    <option value="community">Community Service</option>
                    <option value="education">Education</option>
                    <option value="healthcare">Healthcare</option>
                    <option value="environment">Environment</option>
                    <option value="emergency">Emergency Response</option>
                    <option value="youth">Youth Development</option>
                    <option value="elderly">Elderly Care</option>
                    <option value="other">Other</option>
                  </select>
                  {errors.type && <p className="text-red-500 text-sm mt-1">{errors.type}</p>}
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Current Job/Occupation
                  </label>
                  <input
                    type="text"
                    name="job"
                    value={formData.job}
                    onChange={handleInputChange}
                    className={inputClasses('job')}
                    placeholder="Enter your occupation"
                  />
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Status
                  </label>
                  <select
                    name="status"
                    value={formData.status}
                    onChange={handleInputChange}
                    className={inputClasses('status')}
                  >
                    <option value="">Select Status</option>
                    <option value="active">Active</option>
                    <option value="inactive">Inactive</option>
                    <option value="pending">Pending</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Profile Photo
                  </label>
                  <div className="relative">
                    <input
                      type="file"
                      name="photo"
                      onChange={handleFileChange}
                      accept="image/*"
                      className="hidden"
                      id="photo-upload"
                    />
                    <label
                      htmlFor="photo-upload"
                      className={`
                        flex items-center justify-center w-full p-4 rounded-xl cursor-pointer
                        transition-all duration-300 font-semibold
                        ${photoFileName 
                          ? 'bg-gradient-to-r from-green-600 to-green-500 text-white' 
                          : 'bg-gradient-to-r from-blue-600 to-blue-500 text-white hover:-translate-y-1 hover:shadow-lg'
                        }
                      `}
                    >
                      <Camera className="w-5 h-5 mr-2" />
                      {photoFileName || 'Choose Photo'}
                    </label>
                  </div>
                </div>

                <div className="md:col-span-2">
                  <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                    Description/Additional Information
                  </label>
                  <textarea
                    name="description"
                    value={formData.description}
                    onChange={handleInputChange}
                    rows={4}
                    className={inputClasses('description')}
                    placeholder="Tell us about your interests, skills, and why you want to volunteer..."
                  />
                </div>
              </div>
            </div>

            {/* Submit Button */}
            <button
              type="button"
              onClick={handleSubmit}
              className="
                w-full bg-gradient-to-r from-blue-600 to-blue-500 text-white 
                py-4 px-8 rounded-xl text-lg font-semibold uppercase tracking-wide
                transition-all duration-300 hover:-translate-y-1 hover:shadow-xl
                focus:outline-none focus:ring-4 focus:ring-blue-300
                active:translate-y-0
              "
            >
              Register as Volunteer
            </button>
            </div>
        </div>
      </div>
    </div>
  );
};

export default VolunteerRegistrationForm;